import React, { Component } from 'react';
import { Route } from 'react-router-dom';
import Books from './pages/Books';
import Detail from './pages/Detail';

import './App.css';

class App extends Component {
  render() {
    return (
      <div className="App">
        <Route exact path={ process.env.PUBLIC_URL + "/"} component={Books} ></Route>
        <Route exact path={process.env.PUBLIC_URL + "/detail/:uid" } component={Detail} ></Route>

      </div>
    );
  }
}

export default App;
